<template>
	<view>
		<!-- 顶部内容 --> 	<!-- 顶部背景图片 --><!-- mode="widthFix" -->
		<!-- <view class="" style="padding-top:0">
		
			<image class="topHeadbakimg" :src= "imageRootUrl + 'image/common/headerTopbg.png'" mode="widthFix"></image>
			
			<view class="head-content-fixed " :style="{'background':head_is_fixed == 1?'#FACC3F':''}"
				style="border-bottom: 1rpx solid rgba(0,0,0,0.1);">
				<view class="head-content-fixed-content ">
					<view class="head-top-nav-title center">三明市儿童书屋</view>
				</view>
			</view>
		</view> -->
		<view class="container">
			<view class="contentPanel" style="padding: 10rpx; border-radius: 24rpx;"> <!-- 搜索 -->
				<view class="line70">
					<view class="top_search_btn_iamgePanel" style=""> 
						<image :src= "imageRootUrl + 'image/common/scan.png'"  style=""></image>
					</view>
					<view class="top_search_btn_inputPanel" style="">  
						<!-- <input class="top_search_btn_input" type="text" style="" placeholder="请输入书籍名称、系列、ISBN"/> -->
						<uni-search-bar radius="5"  class="top_search_btn_input" v-model="searchContent"  cancelButton="none"  placeholder="请输入书籍名称、系列、ISBN" >
						</uni-search-bar>
					</view>
					<view class="top_search_btn f26" style="background-color: #c8dcd2;" @click="clickSearch()">搜索</view>
				</view>
			</view>
			<view class="h20"></view>
			<view class="contentPanel ageType"> <!-- 年龄区间 -->
				<view class="line60">
					<view v-for="(item,index) in ageTypeList" :key="index" class="f26 line60 left23 center age "
						:class="{ ageselected: ageTypeIndex === index }" @click="clickAgeType(index)">{{item.name}}
					</view>
				</view>
			</view>
			<view class="h20"></view>
			<view class="contentPanel category" style=""> <!-- :style="{'height':rpxWindowHeight + 'rpx'}" -->
				<view class="books_category_panel">
					<view class="books_category_item_panel " v-for="(item,index) in booksCategoryList" :key="index" :class="{ books_category_active: selectedCategoryIndex === index }" @click="clickBooksCategory(index,item.id)">
						{{item.name}}
					</view>
					<view class="books_category_item_panel">
					</view>  
				</view>
				<view class="books_list_panel">
					<view class="h10"></view>
					<scroll-view  scroll-y="true" class="scroll-Y"   @scrolltolower="lower" :style="'height: calc(100% - 100rpx)'">
						<block v-for="(item, index) in booksList" :key="index">
							<view style="position: relative;overflow: hidden; width: 30%; float: left; margin-left: 2%;" @click="clickToBooksDetail(item)">
								<view style="cursor: pointer;">
									<image :src="adminRootUrl + item.imageFilePath" mode="aspectFit" style=" height: 120rpx;display: block;width: 100% ;"></image>
									<view class="line70 center f24">{{item.book_name}}</view>
									<view class="outOnLoan" v-show="item.isBookAvailable !=1 ">已借出</view>
								</view>
							</view>  
						</block>
						<view class="loading-text">{{loadingText}}</view>
					</scroll-view>
					<view class="clear "></view>
				</view>
				<view class="clear"></view>
			</view>
			<view class="clear"></view>
		</view>
	</view>
</template>

<script>
	import common from "@/js/common.js";
	import config from "@/js/config.js";
	import util from "@/js/util.js";
	let statusBarHeight = uni.getSystemInfoSync().statusBarHeight; // 状态栏 高度
	let screenHeight = uni.getSystemInfoSync().screenHeight; // 屏幕 高度 screenHeight

	let that = null;
	export default {
		data() {
			return {
				url: "http://admin.8630.net/WYB/UploadFile/headerTopbg.png",
				imageRootUrl:config.imageRootUrl,//前端图片地址
				adminRootUrl:config.adminRootUrl +"/",//后端图片地址
				statusBarHeight, // 状态栏 高度
				screenHeight, // 屏幕 高度
				searchContent:'',
				head_is_fixed: 0,
				ageTypeList: [],//{code: 'all',name: '全部'}, {code: 'all',name: '0-2岁'}, {code: 'all',name: '3-6岁'}, {code: 'all',name: '7-10岁'}, {code: 'all',name: '11-14岁'},
				ageTypeIndex: 0, //选中年龄类型index  默认为0 选中全部
				ageID: '',//选中年龄ID
				booksCategoryList: [],//{ id: 1,name: '全部'},{id: 2,name: '绘本认知'}, {id: 3,name: '绘本故事'}, {id: 4,name: '科普百科'}, {id: 5,name: '动漫卡通'}, {id: 6,name: '名人传记'}, {id: 7,name: '国学'},{ id: 8,name: '注音'}, {id: 9,name: '精装'},{id: 10,name: '获奖'}, {id: 11,name: '国际名师大作'}, {id: 11,name: '分级阅读'}, {id: 11,name: '幼儿园故事'}, {id: 11,name: '纸板书'}, {id: 11,name: '通话故事'}, {id: 11,name: '中国传统故事'}, {id: 11,name: '情绪管理'}
				booksList: [],
				// {id: 1, name: '密码花园',url: config.imageRootUrl + 'image/common/bookb1.jpg',},{id: 2,name: '安徒生童话全集',url: config.imageRootUrl + 'image/common/bookb2.jpg',},
				// {id: 3,name: '窗边的小豆豆',url: config.imageRootUrl + 'image/common/bookb3.jpg',}, {id: 4,name: '夏洛的网',url: config.imageRootUrl + 'image/common/bookb4.jpg',},
				// {id: 5,name: '柳林风声',url: config.imageRootUrl + 'image/common/bookb5.jpg',}, {id: 6,name: '格林童话',url: config.imageRootUrl + 'image/common/bookb6.jpg',},
				// {id: 7,name: '100层的火箭',url: config.imageRootUrl + 'image/common/bookb7.jpg',},
				// {id: 1, name: '密码花园1',url: config.imageRootUrl + 'image/common/bookb1.jpg',},{id: 2,name: '安徒生童话全集1',url: config.imageRootUrl + 'image/common/bookb2.jpg',},
				// {id: 3,name: '窗边的小豆豆1',url: config.imageRootUrl + 'image/common/bookb3.jpg',}, {id: 4,name: '夏洛的网1',url: config.imageRootUrl + 'image/common/bookb4.jpg',},
				// {id: 5,name: '柳林风声1',url: config.imageRootUrl + 'image/common/bookb5.jpg',}, {id: 6,name: '格林童话1',url: config.imageRootUrl + 'image/common/bookb6.jpg',},
				// {id: 7,name: '100层的火箭1',url: config.imageRootUrl + 'image/common/bookb7.jpg',},
				// {id: 1, name: '密码花园2',url: config.imageRootUrl + 'image/common/bookb1.jpg',},{id: 2,name: '安徒生童话全集2',url: config.imageRootUrl + 'image/common/bookb2.jpg',},
				// {id: 3,name: '窗边的小豆豆2',url: config.imageRootUrl + 'image/common/bookb3.jpg',}, {id: 4,name: '夏洛的网2',url: config.imageRootUrl + 'image/common/bookb4.jpg',},
				// {id: 5,name: '柳林风声2',url: config.imageRootUrl + 'image/common/bookb5.jpg',}, {id: 6,name: '格林童话2',url: config.imageRootUrl + 'image/common/bookb6.jpg',},
				// {id: 7,name: '100层的火箭2',url: config.imageRootUrl + 'image/common/bookb7.jpg',}
				selectedCategoryIndex: 0,//标签Index
				selectedCategoryID: '',//选中标签ID
				rpxWindowHeight: 0,
				loadingText: "正在加载...",
				showLoadMore: false,
				pageIndex: 0, //页数
				pageSize: 20, //条数
				pageCount: 0, //最大页数
			};
		},
		onReady() {

		},
		onShow() {
			that.booksList=[]
			that.bindData(); //绑定数据
		},
		onPageScroll(e) {
			that.head_is_fixed = e.scrollTop > 0 ? 1 : 0
		},
		onLoad() {
			that = this;
			// alert(new Date().format("yyyy-MM-dd hh:mm:ss:S"));
		},
		methods: {
			clickAgeType(index) { //年龄类型选中
				that.booksList=[]
				that.ageTypeIndex = index
				that.bindGetViewBookSkuAgeGroupList();//绑定书签(分类)
				that.selectedCategoryIndex= 0 
				that.selectedCategoryID= ''
			},
			clickBooksCategory(index,id) { //书签(分类)选中
				that.booksList=[]
				that.selectedCategoryIndex = index
				that.selectedCategoryID = id
				that.bindGetBookInfoList();//绑定图书列表
			},
			bindData() {
				that.bindAgeGroupList(); //绑定年龄列表
			},
			bindAgeGroupList(){ //绑定年龄列表
				that.ageTypeList=[]
				var data = {};
				data.enabled = true;
				data.orderBy = 'display_order';
				common.call("GetAgeGroupList", data, function(result) {
					that.ageTypeList = result.data.dataList 
					console.log(that.ageTypeList)
					that.bindGetViewBookSkuAgeGroupList();//绑定书签列表(分类列表)
				})
			},
			bindGetViewBookSkuAgeGroupList(){//绑定书签列表(分类列表)
				that.booksCategoryList=[]
				var data = {};
				data.ageGroupId = that.ageTypeList[that.ageTypeIndex].id;
				data.companyId = 35
				data.pageSize = 10000
				common.call("GetViewBookTagListByCondition", data, function(result) {
					let bookMarkData= {
						id:0,name:'全部'
					}
					that.booksCategoryList.push(bookMarkData)
					for (let i = 0; i < result.data.length; i++) {
						let bookMarkData = {
							id:result.data[i].id,
							name:result.data[i].name
						}
						that.booksCategoryList.push(bookMarkData)
					}
					console.log(that.booksCategoryList)
					that.bindGetBookInfoList();//绑定书签的图书
				})
			},
			bindGetBookInfoList(){//绑定图书列表
				var data = {};
				data.companyId = 35
				//ageGroupSelectValue   年龄段ID
				//bookTagSelectValue    标签ID
				data.ageGroupSelectValue = that.ageTypeList[that.ageTypeIndex].id//年龄段ID
				if(that.selectedCategoryID!=""){ //标签ID
					data.bookTagSelectValue = that.selectedCategoryID;
				}
				if(that.searchContent!=""){
					data.searchContent = that.searchContent ;
				}
				data.pageIndex = that.pageIndex;
				data.pageSize = that.pageSize;
				data.orderBy = "create_time desc";
				common.call("GetViewBookSkuInfoList", data, function(result) {
					console.log(result)
					that.booksList.push(...result.data.dataList);
					that.pageCount = result.data.pageCount;
					that.pageIndex = result.data.pageIndex;
					that.pageSize = result.data.pageSize;
					if(that.booksList.length== 0 || that.booksList.length<=20){
						that.loadingText="没有更多数据了" 
					}
				})
			},
			clickSearch(){//搜索
				that.booksList=[]
				that.bindGetBookInfoList();//绑定图书列表
			},
			clickToBooksDetail(item){// 跳转到图书详情
				uni.navigateTo({
					url: "/pages/home/booksDetail?bookSkuID=" + item.id
				})
			}, 
			lower(e){
				console.log("滚动到底部")
				if ((that.pageIndex + 1) == that.pageCount) {
					that.loadingText = "没有更多数据了";
				} else if ((that.pageIndex + 1) < that.pageCount) {
					that.pageIndex = that.pageIndex + 1;
					that.showLoadMore = true;
					that.loadingText = "加载更多";
					that.bindGetBookInfoList();//绑定数据
				}
			},
			
		},
	};
</script>

<style lang="scss">
	@import url("/css/common.css");

	page {
		background: #5EAC59;
	}

	.container {
		
	}

	.ageType {
		border-radius: 24rpx;
		padding: 15rpx;
	}

	.category {
		padding: 10rpx;
		border-radius: 24rpx;
		
		/* #ifdef MP-WEIXIN */
			height: calc(100% - 200rpx);
			bottom: -40rpx;
		/* #endif */
		/* #ifdef H5 */
		bottom: 0;
			height: calc(100% - 330rpx);
		/* #endif */
		position: fixed;  
		
		width: 88%;
	}
	.outOnLoan{
			content: "";
		    display: inline-block;
		    width: 95rpx;
		    height: 35rpx;
			line-height: 35rpx;
		    background-size: 100% 100%;
		    vertical-align: middle;
		    position: absolute;
		    z-index: 1;
		    top: 0;
		    right: 0;
		    font-size: 24rpx;
			background-color: #FE613D;
			color: #ffffff;
			text-align: center;
			border-radius: 10rpx;
	}
</style>